import MemoryRecord from "js-memory-record"

export class ModeInfo extends MemoryRecord {
  static get define() {
    return [
      { key: "view", name: "", },
      { key: "play", name: "", },
      { key: "edit", name: "", },
    ]
  }
}

if (process.argv[1] === __filename) {
  console.log(ModeInfo.fetch("simple").key)
  console.log(ModeInfo.fetch("simple").name)
}
