import MemoryRecord from "js-memory-record"
import { Location } from "./location"

export class EditToolInfo extends MemoryRecord {
  static get define() {
    return [

      { key: "",   func: e => { e.xcontainer.hold_pieces_to_piece_box(Location.fetch("white")) }, },
      { key: "", func: e => { e.xcontainer.piece_box_to_hold_pieces(Location.fetch("white")) }, },
      { key: "",       func: e => { e.xcontainer.piece_box_piece_counts_adjust() }, },
      { key: "", func: e => { e.xcontainer.piece_box_reset_by_preset("") }, },
      { key: "",   func: e => { e.xcontainer.piece_box_clear() }, },
      { key: "",     func: e => { e.xcontainer.king_formation_auto_set()   }, },
      { key: "",     func: e => { e.xcontainer.king_formation_auto_unset() }, },
      { key: "",       func: e => { e.api_viewpoint_flip() }, },

      // { key: "",   func: e => { e.xcontainer.shuffle_apply(4) }, },
      { key: "",     func: e => { e.shuffle_dialog_open_handle() }, },
      { key: "",       func: e => { e.xcontainer.slide_xy(0, -1) }, },
      { key: "",       func: e => { e.xcontainer.slide_xy(0, 1)  }, },
      { key: "",       func: e => { e.xcontainer.slide_xy(-1, 0) }, },
      { key: "",       func: e => { e.xcontainer.slide_xy(1, 0)  }, },
      // { key: "()", func: e => { e.xcontainer.king_formation_set("bottom_left") }, },
      // { key: "()", func: e => { e.xcontainer.king_formation_unset("bottom_left") }, },
      // { key: "()", func: e => { e.xcontainer.king_formation_set("bottom_right") }, },
      // { key: "()", func: e => { e.xcontainer.king_formation_unset("bottom_right") }, },

    ]
  }
}
